<template>
  <div id="publish">
    <el-row v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">

          <el-form-item label="" prop="cover">
            <img v-lazyload src="../../static/loading.svg" :data-src="form.cover" data-err="../../static/df.png" class="show-image" v-if="form.cover && form.cover != ''"/>

            <el-input minlength="1" maxlength="150" v-model="form.cover" placeholder=""></el-input>

            <div class="el-upload__tip">
               <a href="https://img9.top/" target="_blank"></a>,  480px  480px, jpg/png <a href="https://tinypng.com/" target="_blank">tinypng.com</a> 
            </div>
          </el-form-item>

          <el-form-item label="" prop="title">
            <el-input minlength="1" maxlength="150" v-model="form.title" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="score">
            <el-rate
              show-text
              v-model="form.score"
              :colors="['#99A9BF', '#F7BA2A', '#FF9900']">
            </el-rate>
          </el-form-item>

          <el-form-item label="" prop="city">
            <el-select v-model="form.city" placeholder="">
              <el-option
                v-for="item in citysOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="addr">
            <el-input minlength="1" maxlength="150" v-model="form.addr" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="content">
            <vue-html5-editor :content="form.content"
                              @change="updateData"
                              :height="350"
                              :z-index="1000"
                              :auto-height="true"
                              :show-module-name="true"
                              placeholder=""></vue-html5-editor>

          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitNode" v-if="!queryId">DApp</el-button>
            <el-button type="primary" @click="modNode" v-if="queryId">DApp</el-button>
            <el-button @click="resetForm('form')" v-if="!queryId"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      queryId: null,
      reload: true,
      citysOptions: ['', '', '', '', '', '', '', '', '', '', ''],
      loading: false,
      form: {
        cover: '',
        title: '',
        score: 0,
        city: '',
        addr: '',
        content: ''
      },
      formRules: {
        cover: [
          {required: true, message: '', trigger: 'blur'}
        ],
        title: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        score: [
          {required: true, message: '', trigger: 'blur'}
        ],
        city: [
          {required: true, message: '', trigger: 'blur'}
        ],
        addr: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 5000, message: ' 1  5000 ', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    updateData: function (d) {
      var vueThis = this;
      vueThis.form.content = d;
    },
    submitNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [f.title, f.cover, f.city, f.addr, f.score, f.content];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'addFood', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    modNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [vueThis.queryId, f.name, f.dtype, f.icon, f.url, f.userName, f.addr, f.txhash, f.desc, f.week, f.reason];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'updateDapp', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    getDetail: function (id) {
      let vueThis = this;
      if (!id) {
        return;
      }
      vueThis.loading = true;
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getFoodInfo', params: [id]}).then((ret) => {
        let data = JSON.parse(ret);
        vueThis.form = data;
        vueThis.loading = false;
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('' + error);
      });
    },
    resetForm: function (formName) {
      var vueThis = this;
      vueThis.reload = false;
      vueThis.$refs[formName].resetFields();
      _.delay(function () {
        vueThis.reload = true;
      }, 300)
    }
  },
  created () {
    this.queryId = this.$route.query.id;
    this.getDetail(this.queryId)
  },
  computed: mapState([
    'accounts'
  ])
}
</script>

<style>
  #publish {
  }

  #publish  .el-form--label-top .el-form-item__label{
    padding: 0px;
  }

  #publish .show-image {
    max-width: 228px;
    max-height: 228px;
    display: block;
  }

  #publish .text-here {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #publish .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }

  #publish .vue-html5-editor img{
    max-width: 90%;
    object-fit: cover;
  }

  #publish .vue-html5-editor>.toolbar>ul>li{
    color: #606266;
  }
</style>



